//在APP端创建H5的视频播放器组件，用此方案，可调整视频组件的层级问题。
//如需扩展其它类型的组件，可按照此方案，自行扩展。
export default {
	name: 'video-render',
	data() {
		return {
			coverShow: true,
		}
	},
	props: {
		isSrc: {
			type: String,
			default: ''
		},
		isNum: {
			type: Number,
			default: 1
		},
		isCover: {
			type: String,
			default: ''
		}
	},
	methods: {
		closeCoverClick() {
			//关闭封面
			this.coverShow = false;
		}
	}
}
